<template>
  <!-------------------------------------- Mocal Windows ---------------------------------->
  <v-app>
    <div class="text-center">
      <v-dialog v-model="this.content.status_modal">
        <v-col cols="12">
          <v-card
            color="black"
            class="mx-auto justify-center align-center ma-0 pa-0"
            min-width="200"
            :max-width="change()"
            plain
          >
            <v-card-text>
              <div>
                <Cropper
                  :src="this.content.clicked_image"
                  ref="cropper"
                  :stencil-props="{ aspectRatio: 16 / 9 }"
                />
              </div>
              <v-text-field
                bg-color="white"
                color="priamry"
                variant="underlined"
                v-show="this.content.text_input_modal"
                name="input-7-4"
                label="이곳에 영상에들어갈 자막을 입력하세요."
                v-model="this.content.text_content"
                @input="
                  text_searchChangeFunc(
                    $event,
                    this.content.text_index,
                    this.content.text_order
                  )
                "
              ></v-text-field>
            </v-card-text>
            <v-card-actions class="justify-start">
              <div class="d-flex" color="white">
                <v-btn
                  size="small"
                  color="white"
                  icon="mdi-magnify-plus-outline"
                  @click="zoomin"
                ></v-btn>
                <v-btn
                  size="small"
                  color="white"
                  icon="mdi-magnify-minus-outline"
                  @click="zoomout"
                ></v-btn>
                <v-btn
                  size="small"
                  color="white"
                  icon="mdi-reload"
                  @click="rotateright"
                ></v-btn>
                <v-btn
                  size="small"
                  color="white"
                  icon="mdi-restore"
                  @click="rotateleft"
                ></v-btn>
                <v-btn
                  size="small"
                  color="white"
                  icon="mdi-crop"
                  @click="crop"
                ></v-btn>
                <v-btn
                  size="small"
                  color="white"
                  icon="mdi-cloud-upload"
                  @click="openFileUpload()"
                ></v-btn>
                <v-file-input
                  @change="eachChange"
                  accept="image/*"
                  id="eachChanges"
                  style="display: none"
                />
              </div>
              <v-spacer></v-spacer>
              <v-btn v-on:click="edit_done">Confirm</v-btn>
            </v-card-actions>
            <cropper></cropper>
          </v-card>
        </v-col>
      </v-dialog>
    </div>
    <!-------------------------------------- Mocal Windows ---------------------------------->

    <!--------------------------------------- Selected Vimeo  ------------------------------->
    <!-- <v-container class="d-flex align-center flex-column" fluid>
    <v-row class="mt-0">
    <v-card class="mx-auto" max-width="800" flat>
    <iframe :src="this.profile.product_type.review_url" width="640" height="360"></iframe>
    </v-card>
    </v-row>
  </v-container> -->

    <!--------------------------------------------------- Outline -->
    <v-img
      :src=profile.store_data.file_03
      cover
      max-height="200"
    ></v-img>

    <v-container class="pa-0 ma-0" fluid>
      <v-row class="justify-center pa-0 ma-8">
        <!-- <div class="content-dot"></div> -->
        <div class="d-flex justify-center">
          <div>
            <img class="content_img" :src="require(`@/assets/leafl.png`)" />
          </div>
          <div>
            Upload your content step by step
          </div>
          <div>
            <img class="content_img" :src="require(`@/assets/leaf.png`)" />
          </div>
          </div>
      </v-row>

      <!----------------------------------------------- Tabs -->
      <v-row>
        <v-card class="mx-auto" max-width="1200" flat>
        <v-divider></v-divider>
        <v-sheet elevation="0">
        <v-tabs
          show-arrows
          slider-color="orange"
          color="orange-accent-4"
        >
          <v-tab
            v-for="(item, idx) in this.profile.product_type.items"
            size="small"
            :key="idx"
            :value="item.id"
            @click="index_selector(idx)"
            >{{ item }}
          </v-tab>
        </v-tabs>
      </v-sheet>
        <v-divider></v-divider>
        </v-card>
      </v-row>
      <!----------------------------------------------- Tabs -->
      <!------------------------------------------- Tab menu ----------------------------------->
      <!-- <v-row class="justify-center pa-0 ma-0">
  
        <v-divider></v-divider>
          <v-btn-toggle 
          v-model="toggle"
          color="deep-white accent-3"
           divided variant="outlined">
            <v-btn
              class="content_button"
              size="small"
              v-for="(item, idx) in this.profile.product_type.items"
              :key="idx"
              @click="index_selector(idx)"
              flat
            >
              {{ item }}
            </v-btn>
          </v-btn-toggle>
          <v-divider></v-divider>  
    </v-row> -->
      <!------------------------------------------------------- Card    -->
      <v-row class="justify-center pa-0 ma-0 mt-4">
        <div>
          <Card :index="this.index"></Card>
        </div>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
/* eslint-disable no-unused-vars */

import { mapState } from "vuex";
import { Cropper } from "vue-advanced-cropper";
import "vue-advanced-cropper/dist/style.css";
import Card from "@/components/Card.vue";

export default {
  name: "ConTent",
  data() {
    return {
      snackbar: true,
      toggle: undefined,
      file: false,
      div: 1,
      index: 0,
    };
  },
  computed: {
    ...mapState(["user", "content", "profile"]),

    mobile_detact() {
      return this.$vuetify.display.mobile;
    },
  },
  unmounted() {
    if (this.profile.product_type.product_type) {
      this.$store.dispatch("content/patch_context");
    }
  },
  components: {
    Card,
    Cropper,
  },
  methods: {
    ///// --------------------------- cropper ------------------
    // Open File Change
    openFileUpload() {
      document.getElementById("eachChanges").click();
    },
    // When Image Click
    change() {
      if (this.$vuetify.display.width < 800) {
        return this.$vuetify.display.width - 20;
      }
      return 800;
    },

    // When Image File Change
    async eachChange(e) {
      this.$store.commit(
        "content/set_clicked_image",
        "https://linst-s3.s3.ap-northeast-2.amazonaws.com/static/images/temp/each_upload.gif"
      );
      let flie = e.target.files;
      const dataFile = new FormData();
      dataFile.append("files", flie[0]);
      await this.$store.dispatch("content/put_eachfiles", {
        id: this.content.clicked_id,
        data: dataFile,
      });
    },

    // When Image Crop
    crop() {
      this.file = null;
      const { canvas } = this.$refs.cropper.getResult();
      if (canvas) {
        const dataFile = new FormData();
        const pathname = new URL(this.content.clicked_image).pathname;
        const filename = pathname.split("/").pop();
        const finalname = Date.now() + "." + filename.split(".").pop();
        canvas.toBlob((blob) => {
          dataFile.append("files", blob, finalname);
          const temp_image =
            "https://linst-s3.s3.ap-northeast-2.amazonaws.com/static/images/temp/each_upload.gif";
          this.$store.commit("content/set_clicked_image", temp_image);
          this.$store.dispatch("content/put_eachfiles", {
            id: this.content.clicked_id,
            data: dataFile,
          });
        });
      }
    },

    //------------>  cropper
    zoomin() {
      this.$refs.cropper.zoom(1.5);
    },
    zoomout() {
      this.$refs.cropper.zoom(0.5);
    },
    rotateright() {
      this.$refs.cropper.rotate(-5);
    },
    rotateleft() {
      this.$refs.cropper.rotate(5);
    },
    edit_done() {
      this.$store.commit("content/set_status_modal", false);

      if (this.profile.product_type.product_type) {
        this.$store.dispatch("content/patch_context");
      }
    },
    //------------>  cropper

    //------------>  cropper text
    text_searchChangeFunc(event, index, order) {
      this.content.content_user_data.sub_title[index].text_array[order].text =
        event.target.value;
    },

    // Sort patch
    index_selector(idx) {
      // update sort by selected index
      if (this.profile.product_type.product_type) {
        this.$store.dispatch("content/sort_save", { index: this.index });
        this.index = idx;
      }
    },

    imageEdit(element, index, order, list, content) {
      // console.log(element.files);
      // console.log(element.id);
      this.$store.commit("content/set_status_modal", true);
      this.$store.commit("content/set_clicked_image", element.files);
      this.$store.commit("content/set_clicked_id", element.id);
      // text input box show or not
      let input_box = this.text_subtitle_show(order, list);

      if (input_box === true) {
        this.$store.commit("content/set_status_text_input", true);
      } else {
        this.$store.commit("content/set_status_text_input", false);
      }

      let text_input = this.text_subtitle_readcontext(index, order, content);
      this.$store.commit("content/set_text_input", text_input);
      this.$store.commit("content/set_text_index", index);
      this.$store.commit("content/set_text_order", order);
    },
  },
};
/* eslint-enable no-unused-vars */
</script>

<style>
.vue-advanced-cropper__image {
  filter: blur(0px) saturate(0.5);
}
.vue-preview__image {
  --width: 1;
  filter: brightness(var(--width));
}
.custom-tab-items {
  color: rgb(190, 190, 190);
  background: rgb(65, 65, 65);
}

.button {
  background-color: #ff9100;
  border: none;
  color: white;
  padding: 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  margin: 4px 2px;
}

.button1 {
  border-radius: 2px;
}
.button2 {
  border-radius: 4px;
}
.button3 {
  border-radius: 8px;
}
.button4 {
  border-radius: 12px;
}
.button5 {
  border-radius: 50%;
}
.v-application__wrap {
  min-height: 100%;
}

.content-dot {
  width: 20px;
  height: 20px;

  margin: 50px 0px 0px 0px;

  border-radius: 100%;
  background-color: orange;
  opacity: 100%;
  z-index: 9999;
}
.content-tabs {
  margin: 30px 50px 10px 50px;
  justify-content: center;
}
.content_img{
  margin:0px 10px;
  width:80px;
  height:auto;
  opacity: 80%;
}
.content_tabgroup{
  text-align: center;
  justify-content: center;
}
</style>
