<template>
  <v-app>
    <main>
      <!-- MODAL -->
      <div>
      <v-dialog v-model="overlay">
        <div class="card-modal">
          <iframe class="card_iframe" :src="guide_ct"></iframe>
        </div>
      </v-dialog>
    </div>
      <!-- MODAL -->

      <v-container class="ma-0 pa-2 card-container">
        <v-row>
          <v-col>
            <div class="card-head">
              <span><v-btn 
              size="small"
              prepend-icon="mdi-cloud-upload"
              variant="outlined" @click="fileUploadBtn()"
                >Images</v-btn
              >
              <a class="text-caption"
              >&nbsp;&nbsp; {{ description_ct }} {{ img_ct }} 장의 사진 필요</a></span>
              <span>
                  Show guide
                  <v-icon @click="overlay = true;">mdi-play-circle-outline</v-icon>
              </span>
              
              <!-- <v-divider class="mt-1" color="white"></v-divider> -->
              <v-file-input
                id="fileUpload"
                style="display:none"
                accept="image/*"
                @change="photo_upload($event, index)"
                multiple
              />
              </div>
            <draggable
              class="d-flex card-drg"
              v-model="this.content.file_data[index]"
              animation="100"
              :sort="true"
              item-key="id"
              @change="photo_dragging(this.content.file_data[index], index)"
            >
              <template #item="{ element }">
                <div class="card-card">
                  <img
                    class="card-img"
                    :src="element.thumbnail"
                    @click="image_click(element)"
                    :class="
                      element.id === this.content.clicked_id
                        ? 'card-active'
                        : 'card-deactive'
                    "
                  />
                  <div  class="card-card-overlay">
                    <div>
                    <v-icon
                      size="small"
                      color="white"
                      @click="
                        imageEdit(
                          element,
                          index,
                          element.order,
                          this.text_show_input_box[index],
                          content.content_user_data.sub_title
                        )
                      "
                    >
                      mdi-crop
                    </v-icon>
                    <v-icon
                      size="small"
                      color="red"
                      @click="
                        imageEdit(
                          element,
                          index,
                          element.order,
                          this.text_show_input_box[index],
                          content.content_user_data.sub_title
                        )
                      "
                      v-show="
                        text_subtitle_show(
                          element.order,
                          this.text_show_input_box[index]
                        )
                      "
                    >
                      mdi-format-text-variant-outline
                    </v-icon>
                    </div>
                  </div>
                </div>
              </template>
            </draggable>
          </v-col>
        </v-row>
        
        <div class="pa-3">
    <div class="d-flex text-caption text-white justify-center">
        <div class="guide_div">
            <v-icon>mdi-crop</v-icon>
            이미지 편집 &nbsp;
        </div>
        <div class="guide_div">
            <v-icon>mdi-format-text-variant-outline</v-icon>
            자막이 필요한 사진 &nbsp;
        </div>
        <div class="guide_div">
            <v-icon>mdi-cloud-upload</v-icon>
            파일을 업로드 &nbsp;
        </div>
        <div class="guide_div">
            <v-icon>mdi-mouse-move-vertical</v-icon>
            드래그하여 순서이동
        </div>
      </div>
    <div>
      <v-btn>>Final Summit</v-btn>
    </div>
    </div>

      </v-container>

      
    </main>
    {{ mobile_detact }}

  </v-app>
</template>

<script>
// import { useDisplay } from 'vuetify'
import draggable from "vuedraggable";
import { mapState, mapActions, mapMutations } from "vuex";
import {
  subtitle_showwindow,
  subtitle_readcontext,
} from "@/assets/subtitle.js";

export default {
  name: "CaRd",
  props: {
    index: Number,
  },
  data() {
    return {
      overlay: false,
      show: true,
      image_count: this.test()
    };
  },
  computed: {
    ...mapState(["user", "content", "profile"]),

    text_show_input_box() {
      let bbb = JSON.parse(this.profile.product_type.input_box);
      return bbb;
    },
    mobile_detact() {
      return this.$vuetify.display.mobile;
    },
    img_ct() {
      let tt = this.profile.product_type.counts || "true"
      let con_json = JSON.parse( tt)
      return con_json[this.index]
    },
    description_ct() {
      let tt = this.profile.product_type.description_index || "true"
      let con_json = JSON.parse( tt)
      return con_json[this.index]
    },
    guide_ct() {
      let tt = this.profile.product_type.parts || "true"
      let con_json = JSON.parse( tt)
      return con_json[this.index]
    },
  },
  unmounted() {
    if (this.profile.product_type.product_type) {
      this.$store.dispatch("content/sort_save", { index: this.index });
      console.log( "order updated")
    }
  },
  components: {
    draggable,
  },
  methods: {
    ...mapMutations(["set_user_status"]),
    ...mapActions(["user_verify"]),
    //-------------------- upload multiple files
    test(){
    },
    fileUploadBtn() {
      document.getElementById("fileUpload").click();
    },

    image_click(element) {
      console.log("clicked")
      this.$store.commit("content/set_clicked_image", element.files);
      this.$store.commit("content/set_clicked_id", element.id);

    },

    text_subtitle_show(index, listshow) {
      return subtitle_showwindow(index, listshow);
    },

    text_subtitle_readcontext(index, order, list) {
      return subtitle_readcontext(index, order, list);
    },
    text_searchChangeFunc(event, index, order) {
      this.content.content_user_data.sub_title[index].text_array[order].text =
        event.target.value;
    },
    photo_upload(event, index) {

      let index_file = event.target.files;
      let count_for = index_file.length;

      if ( index_file.length > this.img_ct){
        count_for = this.img_ct
      }

      for (var i = 0; i < count_for; i++) {
        this.content.file_data[index][i].thumbnail =
          "https://linst-s3.s3.ap-northeast-2.amazonaws.com/static/images/temp/uploding_anim.gif";
        const dataFile = new FormData();
        const file_id = this.content.file_data[index][i].id;
        dataFile.append("files", index_file[i]);
        dataFile.append("id", file_id);
        this.$store.dispatch("content/put_files", {
          id: file_id,
          data: dataFile,
          config: this.user.config,
          index: index,
        });
      }
    },
    current(e, index, id) {
      console.log("click works1111");
      e, index, id;
    },
    photo_dragging(count, index) {
      for (var i = 0; i < count.length; i++) {
        // 배열 arr의 모든 요소의 인덱스(index)를 출력함.
        this.content.file_data[index][i].order = i;
        this.content.file_data[index][i].index = i;
        // console.log( this.content.content_user_data.sub_title)
      }
      // this.$store.dispatch('content/sort_save', {index:this.index})
    },

    imageEdit(element, index, order, list, content) {
      // console.log(element.files);
      // console.log(element.id);
      this.$store.commit("content/set_status_modal", true);
      this.$store.commit("content/set_clicked_image", element.files);
      this.$store.commit("content/set_clicked_id", element.id);
      // text input box show or not
      let input_box = this.text_subtitle_show(order, list);

      if (input_box === true) {
        this.$store.commit("content/set_status_text_input", true);
      } else {
        this.$store.commit("content/set_status_text_input", false);
      }

      let text_input = this.text_subtitle_readcontext(index, order, content);
      this.$store.commit("content/set_text_input", text_input);
      this.$store.commit("content/set_text_index", index);
      this.$store.commit("content/set_text_order", order);
    },
  },
};
</script>

<style>
.card-container{
  background: #ff9100;
  border-radius: 12px;
}
.card-drg {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 0px 4px 0px 4px;
  padding: 3px;
  justify-content: start;
}
.card-card {
  position: relative;
}
.card-card-overlay {
  position: absolute;
  top: 0;
  left: 0;
}
.card-deactive {
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%;
  filter: grayscale(30%) brightness(70%);
}
.card-active {
  padding: 0px;
  width: 100%;
  height: 100%;
  filter: grayscale(0%);
  box-shadow: 0 0 10px #07c;
  outline: 2px solid rgb(0, 138, 236);
  outline-offset: -1px;
}
.card-img {
  padding: 0px;
  margin: 0px;
  width: 105px;
  height: 100%;
  /* height:70px; */
  /* filter: grayscale(100%); */
  /* background-size: 100% 100%; */
  /* background-size: contain; */
  /* flex-grow:1; */
}
.card-img:hover {
  width: 105px;
  height: 100%;
  filter: grayscale(0%);
  box-shadow: 0 0 10px #07c;
  outline: 2px solid rgb(0, 138, 236);
  outline-offset: -1px;
}
.card-head{
  display: flex;
  padding: 2px;
  padding-bottom: 5px;
  color: white;
  background: transparent;
  justify-content: space-between;
}
.card-modal{
  height: 100%;
  width: 100%;
  background: transparent;
}
.card_iframe {
  display: block;
  border: none;
  height: 40vh;
  width: 80vw;
}
</style>